	  			transform-origin: 0% 0%;
	  			transition: transform 0.4s ease-in-out; } } }


	.nav-container {
		.hamburger-lines {
			.line2 {
	  			transition: transform 0.2s ease-in-out; } } }


	.nav-container {
		.hamburger-lines {
			.line3 {
	  			transform-origin: 0% 100%;
	  			transition: transform 0.4s ease-in-out; } } }


